'use client'
import * as React from "react";
import { useState, useRef, useEffect } from "react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { 
  Search, 
  Plus, 
  Send, 
  User, 
  Users, 
  Paperclip, 
  Camera,
  MoreVertical
} from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

// Types
type MessageType = "sent" | "received";
interface Message {
  id: string;
  content: string;
  senderId: string;
  timestamp: Date;
  type: MessageType;
}

interface User {
  id: string;
  name: string;
  avatar?: string;
  isOnline?: boolean;
}

interface Chat {
  id: string;
  name: string;
  isGroup: boolean;
  participants: User[];
  messages: Message[];
  lastMessage?: string;
  lastMessageTime?: Date;
}

// Mock data
const mockUsers: User[] = [
  { id: "1", name: "Alice Johnson", avatar: "https://placehold.co/100x100" },
  { id: "2", name: "Bob Smith", avatar: "https://placehold.co/100x100" },
  { id: "3", name: "Carol Davis", avatar: "https://placehold.co/100x100" },
];

const mockChats: Chat[] = [
  {
    id: "1",
    name: "Alice Johnson",
    isGroup: false,
    participants: [mockUsers[0]],
    messages: [
      { id: "1", content: "Hi there!", senderId: "1", timestamp: new Date(Date.now() - 3600000), type: "received" },
      { id: "2", content: "Hello Alice!", senderId: "me", timestamp: new Date(Date.now() - 3500000), type: "sent" },
    ],
    lastMessage: "Hello Alice!",
    lastMessageTime: new Date(Date.now() - 3500000),
  },
  {
    id: "2",
    name: "Project Team",
    isGroup: true,
    participants: mockUsers,
    messages: [
      { id: "1", content: "Meeting at 3 PM", senderId: "2", timestamp: new Date(Date.now() - 1800000), type: "received" },
      { id: "2", content: "I'll be there!", senderId: "me", timestamp: new Date(Date.now() - 1700000), type: "sent" },
    ],
    lastMessage: "I'll be there!",
    lastMessageTime: new Date(Date.now() - 1700000),
  },
];

// Components
const ChatMessage = ({ message }: { message: Message }) => {
  return (
    <div className={`flex ${message.type === "sent" ? "justify-end" : "justify-start"} mb-4`}>
      <div className={`max-w-xs md:max-w-md lg:max-w-lg px-4 py-2 rounded-2xl ${
        message.type === "sent" 
          ? "bg-primary text-primary-foreground rounded-br-md" 
          : "bg-muted rounded-bl-md"
      }`}>
        <p>{message.content}</p>
        <p className={`text-xs mt-1 ${message.type === "sent" ? "text-primary-foreground/70" : "text-muted-foreground"}`}>
          {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
        </p>
      </div>
    </div>
  );
};

const ChatList = ({ 
  chats, 
  activeChatId, 
  setActiveChatId 
}: { 
  chats: Chat[]; 
  activeChatId: string | null; 
  setActiveChatId: (id: string) => void;
}) => {
  return (
    <ScrollArea className="h-full">
      {chats.map(chat => (
        <div 
          key={chat.id}
          className={`flex items-center p-3 cursor-pointer border-l-4 ${
            activeChatId === chat.id 
              ? "bg-accent border-l-primary" 
              : "border-l-transparent hover:bg-accent/50"
          }`}
          onClick={() => setActiveChatId(chat.id)}
        >
          <Avatar className="h-10 w-10 mr-3">
            {chat.isGroup ? (
              <div className="relative">
                <AvatarImage src={chat.participants[0]?.avatar} />
                <div className="absolute bottom-0 right-0 bg-primary text-primary-foreground rounded-full w-4 h-4 flex items-center justify-center">
                  <Users className="h-2 w-2" />
                </div>
              </div>
            ) : (
              <AvatarImage src={chat.participants[0]?.avatar} />
            )}
            <AvatarFallback>{chat.name.charAt(0)}</AvatarFallback>
          </Avatar>
          
          <div className="flex-1 min-w-0">
            <div className="flex justify-between items-center">
              <p className="font-medium truncate">{chat.name}</p>
              {chat.lastMessageTime && (
                <p className="text-xs text-muted-foreground">
                  {chat.lastMessageTime.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                </p>
              )}
            </div>
            <p className="text-sm text-muted-foreground truncate">
              {chat.lastMessage || "No messages yet"}
            </p>
          </div>
        </div>
      ))}
    </ScrollArea>
  );
};

const ChatInput = ({ onSend }: { onSend: (message: string) => void }) => {
  const [message, setMessage] = useState("");
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleSend = () => {
    if (message.trim()) {
      onSend(message);
      setMessage("");
    }
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    // Handle file upload (in a real app)
    const file = e.target.files?.[0];
    if (file) {
      console.log("Uploading file:", file.name);
      // Reset input
      if (fileInputRef.current) {
        fileInputRef.current.value = "";
      }
    }
  };

  return (
    <div className="flex items-center p-2 border-t">
      <Button 
        variant="ghost" 
        size="icon" 
        className="h-9 w-9"
        onClick={() => fileInputRef.current?.click()}
      >
        <Paperclip className="h-4 w-4" />
        <input 
          type="file" 
          ref={fileInputRef} 
          className="hidden" 
          onChange={handleFileUpload}
          accept="image/*,.pdf,.doc,.docx"
        />
      </Button>
      
      <Input
        value={message}
        onChange={(e) => setMessage(e.target.value)}
        placeholder="Type a message..."
        className="flex-1 mx-2"
        onKeyDown={(e) => {
          if (e.key === "Enter" && !e.shiftKey) {
            e.preventDefault();
            handleSend();
          }
        }}
      />
      
      <Button 
        variant="default" 
        size="icon" 
        className="h-9 w-9"
        onClick={handleSend}
        disabled={!message.trim()}
      >
        <Send className="h-4 w-4" />
      </Button>
    </div>
  );
};

export function CommunicationAdmin() {
  const [chats, setChats] = useState<Chat[]>(mockChats);
  const [activeChatId, setActiveChatId] = useState<string | null>(mockChats[0]?.id || null);
  const [newChatName, setNewChatName] = useState("");
  const [isCreatingGroup, setIsCreatingGroup] = useState(false);
  const [selectedParticipants, setSelectedParticipants] = useState<string[]>([]);
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  const activeChat = chats.find(chat => chat.id === activeChatId) || null;
  
  const handleSendMessage = (content: string) => {
    if (!activeChat) return;
    
    const newMessage: Message = {
      id: Date.now().toString(),
      content,
      senderId: "me",
      timestamp: new Date(),
      type: "sent"
    };
    
    setChats(prev => 
      prev.map(chat => 
        chat.id === activeChatId 
          ? { ...chat, messages: [...chat.messages, newMessage], lastMessage: content, lastMessageTime: new Date() }
          : chat
      )
    );
  };
  
  const handleCreateChat = () => {
    if (!newChatName.trim()) return;
    
    const newChat: Chat = {
      id: Date.now().toString(),
      name: newChatName,
      isGroup: false,
      participants: [{ id: "new", name: newChatName, avatar: undefined }],
      messages: [],
    };
    
    setChats(prev => [newChat, ...prev]);
    setActiveChatId(newChat.id);
    setNewChatName("");
  };
  
  const handleCreateGroup = () => {
    if (!newChatName.trim() || selectedParticipants.length === 0) return;
    
    const participants = selectedParticipants.map(id => 
      mockUsers.find(u => u.id === id) || { id, name: `User ${id}`, avatar: undefined }
    );
    
    const newChat: Chat = {
      id: Date.now().toString(),
      name: newChatName,
      isGroup: true,
      participants: [{ id: "me", name: "Me", avatar: undefined }, ...participants],
      messages: [],
    };
    
    setChats(prev => [newChat, ...prev]);
    setActiveChatId(newChat.id);
    setNewChatName("");
    setSelectedParticipants([]);
    setIsCreatingGroup(false);
  };
  
  const toggleParticipantSelection = (id: string) => {
    setSelectedParticipants(prev => 
      prev.includes(id) 
        ? prev.filter(pid => pid !== id) 
        : [...prev, id]
    );
  };
  
  const handleProfileImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      // In a real app, you would upload to your server
      const reader = new FileReader();
      reader.onload = (e) => {
        const imageUrl = e.target?.result as string;
        // Update user's profile image in your state management system
        console.log("New profile image:", imageUrl);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div className="flex h-[80vh] border rounded-lg overflow-hidden">
      {/* Sidebar */}
      <div className="w-full md:w-80 flex flex-col border-r">
        <div className="p-4 border-b flex justify-between items-center">
          <div className="flex items-center space-x-3">
            <Avatar className="h-10 w-10">
              <AvatarImage src="https://placehold.co/100x100" />
              <AvatarFallback>ME</AvatarFallback>
            </Avatar>
            <div>
              <p className="font-medium">My Account</p>
              <p className="text-xs text-muted-foreground">Online</p>
            </div>
          </div>
          
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon">
                <MoreVertical className="h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuItem>
                <Camera className="mr-2 h-4 w-4" />
                <span>Update Profile Photo</span>
                <input 
                  type="file" 
                  ref={fileInputRef} 
                  className="hidden" 
                  onChange={handleProfileImageUpload}
                  accept="image/*"
                />
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
        
        <div className="p-2">
          <div className="relative">
            <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
            <Input 
              placeholder="Search chats..." 
              className="pl-10" 
            />
          </div>
        </div>
        
        <Tabs defaultValue="chats" className="flex-1 flex flex-col">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="chats">Chats</TabsTrigger>
            <TabsTrigger value="contacts">Contacts</TabsTrigger>
          </TabsList>
          
          <TabsContent value="chats" className="flex-1">
            <ChatList 
              chats={chats} 
              activeChatId={activeChatId} 
              setActiveChatId={setActiveChatId} 
            />
          </TabsContent>
          
          <TabsContent value="contacts" className="flex-1 p-0">
            <ScrollArea className="h-full">
              <div className="p-2">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="font-medium">New Chat</h3>
                  <Button 
                    variant="outline" 
                    size="sm"
                    onClick={() => {
                      setNewChatName("");
                      setIsCreatingGroup(false);
                    }}
                  >
                    <Plus className="h-4 w-4 mr-2" />
                    New Chat
                  </Button>
                </div>
                
                {isCreatingGroup ? (
                  <Card>
                    <CardHeader>
                      <CardTitle>Create Group</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <Input
                        placeholder="Group name"
                        value={newChatName}
                        onChange={(e) => setNewChatName(e.target.value)}
                        className="mb-3"
                      />
                      
                      <p className="text-sm font-medium mb-2">Select participants</p>
                      <div className="max-h-40 overflow-y-auto space-y-2">
                        {mockUsers.map(user => (
                          <div 
                            key={user.id} 
                            className="flex items-center p-2 hover:bg-accent rounded cursor-pointer"
                            onClick={() => toggleParticipantSelection(user.id)}
                          >
                            <Avatar className="h-8 w-8 mr-3">
                              <AvatarImage src={user.avatar} />
                              <AvatarFallback>{user.name.charAt(0)}</AvatarFallback>
                            </Avatar>
                            <span>{user.name}</span>
                            {selectedParticipants.includes(user.id) && (
                              <Badge className="ml-auto">Selected</Badge>
                            )}
                          </div>
                        ))}
                      </div>
                      
                      <div className="flex justify-end space-x-2 mt-4">
                        <Button 
                          variant="outline" 
                          onClick={() => setIsCreatingGroup(false)}
                        >
                          Cancel
                        </Button>
                        <Button onClick={handleCreateGroup}>
                          Create Group
                        </Button>
                      </div>
                    </CardContent>
                  </Card>
                ) : (
                  <Card>
                    <CardHeader>
                      <CardTitle>New Chat</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <Input
                        placeholder="Contact name"
                        value={newChatName}
                        onChange={(e) => setNewChatName(e.target.value)}
                        className="mb-3"
                      />
                      <div className="flex space-x-2">
                        <Button 
                          variant="outline" 
                          onClick={() => setIsCreatingGroup(true)}
                        >
                          <Users className="h-4 w-4 mr-2" />
                          Group
                        </Button>
                        <Button onClick={handleCreateChat}>
                          <User className="h-4 w-4 mr-2" />
                          Contact
                        </Button>
                      </div>
                    </CardContent>
                  </Card>
                )}
              </div>
            </ScrollArea>
          </TabsContent>
        </Tabs>
      </div>
      
      {/* Chat Area */}
      <div className="flex-1 flex flex-col">
        {activeChat ? (
          <>
            {/* Chat Header */}
            <div className="p-4 border-b flex items-center">
              <Avatar className="h-10 w-10 mr-3">
                {activeChat.isGroup ? (
                  <div className="relative">
                    <AvatarImage src={activeChat.participants[0]?.avatar} />
                    <div className="absolute bottom-0 right-0 bg-primary text-primary-foreground rounded-full w-4 h-4 flex items-center justify-center">
                      <Users className="h-2 w-2" />
                    </div>
                  </div>
                ) : (
                  <AvatarImage src={activeChat.participants[0]?.avatar} />
                )}
                <AvatarFallback>{activeChat.name.charAt(0)}</AvatarFallback>
              </Avatar>
              <div>
                <p className="font-medium">{activeChat.name}</p>
                <p className="text-xs text-muted-foreground">
                  {activeChat.isGroup 
                    ? `${activeChat.participants.length} members` 
                    : activeChat.participants[0]?.isOnline 
                      ? "Online" 
                      : "Offline"}
                </p>
              </div>
            </div>
            
            {/* Messages */}
            <ScrollArea className="flex-1 p-4">
              <div className="space-y-4">
                {activeChat.messages.map(message => (
                  <ChatMessage key={message.id} message={message} />
                ))}
              </div>
            </ScrollArea>
            
            {/* Input */}
            <ChatInput onSend={handleSendMessage} />
          </>
        ) : (
          <div className="flex-1 flex items-center justify-center">
            <div className="text-center p-4">
              <Users className="h-16 w-16 text-muted-foreground mx-auto mb-4" />
              <p className="text-lg font-medium">Select a chat to start messaging</p>
              <p className="text-muted-foreground">Or create a new chat from the contacts tab</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
